// 5 columns
.col-xs-15,
.col-sm-15,
.col-md-15,
.col-lg-15 {
	position: relative;
	min-height: 1px;
	padding-right: 10px;
	padding-left: 10px;
}

.col-md-15 {
    width: 20%;
    float: left;
}

@media (min-width: 768px) {
	.col-md-15 {
	        width: 20%;
	        float: left;
	    }
	}
@media (min-width: 992px) {
    .col-md-15 {
        width: 20%;
        float: left;
    }
}
@media (min-width: 1200px) {
    .col-md-15 {
        width: 20%;
        float: left;
    }
}